body {
    position: relative;
    background-color: #eee;
    background-color: var(--backgroundGray);
    font-family: proxima-nova, sans-serif;
}

.center-block {
    margin: 0 auto;
    float: none;
}

/** Global Variables */
:root {
    --primaryBlue: #01579b;
    --lakeBlue: #004baf;
    --riverBlue: #097dbc;
    --melloBlue: #049fd9;
    --mediumBlue: #34adde;
    --statusBlue: #64bbe3;
    --icyBlue: #c4d6ed;
    --darkGray1: #39393b;
    --darkGray2: #626469;
    --mediumGray1: #9e9ea2;
    --lightGray1: #c6c7ca;
    --lightGray2: #e9e9e9;
    --paleGray1: #e8ebf1;
    --backgroundGray: #eee;
    --foregroundWhite: #fafafa;
}


.login-container {
    border-radius: 0.25rem;
    margin: 10% auto;
    float: none;
    width: 90%;
    max-width: 25rem;
    padding: 1rem;
    overflow: hidden;
    background: var(--foregroundWhite);
    box-shadow: rgba(0, 0, 0, 0.15) 0 0.06rem 0.06rem 0,
        rgba(0, 0, 0, 0.01) 0 0.06rem 0.1rem 0;
    border: 0.06rem solid #ebebeb;
}

.login-container .title {
    color: var(--melloBlue);
    font-family: proxima-nova;
    font-weight: 500;
    line-height: normal;
    font-size: 5.5rem;
    text-shadow: 0.15rem 0.2rem 0.2rem rgba(0, 0, 0, 0.15);
    margin: 3rem auto 2.5rem auto;
}

.login-btn {
    border-radius: 0.5rem;
    overflow: hidden;
    width: 90%;
    max-width: 60%;
    margin: 2.5rem auto 5rem auto;
    background: var(--mediumBlue);
    border: 0;
    border-bottom: 2px solid var(--riverBlue);
    box-shadow: rgba(0, 0, 0, 0.15) 0 0.06rem 0.06rem 0,
        rgba(0, 0, 0, 0.01) 0 0.06rem 0.1rem 0;
    padding: 0.75rem 0.5rem;
    font-size: 1rem;
    color: white;
}

.login-btn:hover {
    background: var(--melloBlue);
    color: white;
}

